import React from 'react'
import Table from '../components/Table'
const table = () =>{
    const content = {
        titles: ['','',''],
        data:[
            {
                name:'',
                chinese:'90',
                math:'82'
            },
            {
                name:'',
                chinese:'91',
                math:'88'
            },
            {
                name:'',
                chinese:'60',
                math:'99'
            },
        ],
        keys:['name','chinese','math']
    }
    return <Table content={content} />
}
export default table


